.items{
    width: 300px;
    padding-top: 10px;
}
.items p{
    margin: 6px 0px;
}
.item-prices{
    display: flex;
    gap: 20px
}
.item-price-new{
    color: #374151;
    font-size: 16px;
    font-weight: 600;
}
.item-price-old{
    color: #8c8c8c;
    font-size: 16px;
    font-weight: 500;
    text-decoration: line-through;
}
.items:hover{
    transform: scale(1.07);
    transition: 0.6s;
    height: 2px;
    border-radius: 8px;
    
}
