.new-collections{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin-bottom: 65px;
}
.new-collections h1{
    color: #171717;
    font-size: 30px;
    font-weight: 600;
}
.new-collections hr{
    width: 150px;
    height: 4px;
    border-radius: 10px;
    background: #252525
}
.collections{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 80px;
    gap: 20px
}