.popular{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 90vh
}
.popular h1{
    color: #171717;
    font-size: 30px;
    font-weight: 600;
}
.popular hr{
    width: 150px;
    height: 4px;
    border-radius: 10px;
    background: #252525
}
.popular-item{
    margin-top: 50px;
    display: flex;
    gap: 10px
}