.descriptionbox{
    margin: 50px 140px;
}
.descriptionbox-navigator{
    display: flex
}
.descriptionbox-nav-box{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    font-weight: 400;
    width: 140px;
    height: 50px;
    border: 1px solid #d0d0d0
}
.descriptionbox-nav-box .fade{
    background: #FBFBFB;
    color: #555
}
.descriptionbox-description{
    display: flex;
    flex-direction: column;
    gap: 25px;
    border: 1px solid #D0D0D0;
    padding: 40px;
    padding-bottom: 40px;
}